.note-grid{
    display:grid;
    grid-template-columns:  1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    font-size: 150%;
    grid-auto-rows: auto;
    height:100%;
    text-align:center;
    width:66vw;
}
.note-cell {
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
}
.interval-cell {
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: end;
        align-items: flex-end;
}



